.skills_Container h1 {
  font-size: 40px;
}

.skills_Container ul {
  padding-left: 0;
  list-style: none;
  font-size: 3rem;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 140px;
}

.skills_Container ul li i {
  color: #868e96;
}

.skills_Container ul li i:hover {
  color: #645beb;
}

.skills_Container ul li p {
  color: #868e96;
  font-size: 1.4rem;
}

.img_crop {
  min-width: 200px;
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.img_crop>img {
  filter: grayscale(100%);
  opacity: 0.6;
  max-width: 100px;
  max-height: 90px;
  display: block;
  max-width: initial;
}

.img_crop img:hover {
  filter: grayscale(0%);
  opacity: 0.8;
}
